export const fetchAIResponse = async (
  apiUrl,
  apiKey,
  modelName,
  messages,
  temperature = 1,
  stream = false,
  onDataReceived,
  controller
) => {
  // 
  //console.log('apiKey:', apiKey)
  //console.log('apiUrl:', apiUrl)
  //console.log('modelName:', modelName)
  const requestBody = {
    model: modelName,
    messages,
    stream, //  stream 
  };

  //  deepThinking  temperature 
  if (modelName !== "deepseek-reasoner") {
    requestBody.temperature = temperature;
  }

  // 
  const startTime = performance.now();

  const response = await fetch(apiUrl, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${apiKey}`,
    },
    body: JSON.stringify(requestBody), // 
    signal: controller.signal, //  AbortController
  });

  if (!response.ok) {
    throw new Error(`HTTP error! Status: ${response.status}`);
  }

  //  stream  false
  if (!stream) {
    const data = await response.json();
    const endTime = performance.now();
    const totalDuration = parseFloat(((endTime - startTime) / 1000).toFixed(1)); //  1 
    onDataReceived({ type: "complete", duration: totalDuration }); // 
    return data;
  }

  // 
  const reader = response.body.getReader();
  const decoder = new TextDecoder("utf-8");
  let buffer = "";

  // eslint-disable-next-line no-constant-condition
  while (true) {
    const { done, value } = await reader.read();
    if (done) {
      const endTime = performance.now();
      const totalDuration = parseFloat(
        ((endTime - startTime) / 1000).toFixed(1)
      ); //  1 
      onDataReceived({ type: "complete", duration: totalDuration }); // 
      break;
    }

    buffer += decoder.decode(value, { stream: true });

    const lines = buffer.split("\n");
    buffer = lines.pop();

    for (const line of lines) {
      if (line.trim() === "data: [DONE]") {
        return;
      }

      if (line.startsWith("data: ")) {
        const jsonData = line.slice(6);
        try {
          const data = JSON.parse(jsonData);
          if (data.choices && data.choices[0].delta) {
            const delta = data.choices[0].delta;
            const currentDuration = parseFloat(
              ((performance.now() - startTime) / 1000).toFixed(2)
            );

            if (delta.reasoning_content || delta.reasoning) {
              const tokenCount = calculateTokenCount(
                delta.reasoning_content || delta.reasoning
              );
              onDataReceived({
                type: "reasoning",
                content: delta.reasoning_content || delta.reasoning,
                token: tokenCount,
                duration: currentDuration,
              });
            } else if (delta.content) {
              const tokenCount = calculateTokenCount(delta.content);
              onDataReceived({
                type: "content",
                content: delta.content,
                token: tokenCount,
                duration: currentDuration,
              });
            }
          }
        } catch (error) {
          console.error("Error parsing JSON:", error);
        }
      }
    }
  }
};

// TTS
export const fetchTTS = async (apiUrl, apiKey, modelName, input, voice) => {
  const requestBody = {
    model: modelName,
    voice: voice,
    input: input,
  };

  const response = await fetch(apiUrl, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${apiKey}`,
    },
    body: JSON.stringify(requestBody),
  });

  if (!response.ok) {
    throw new Error(`HTTP error! Status: ${response.status}`);
  }

  // 
  const blob = await response.blob();
  return blob;
};

//  token 
const calculateTokenCount = (text) => {
  const englishChars = text.match(/[a-zA-Z]/g) || [];
  const chineseChars = text.match(/[\u4e00-\u9fa5]/g) || [];
  const tokenCount = englishChars.length * 0.3 + chineseChars.length * 0.6;
  return parseFloat(tokenCount.toFixed(4)); //  4 
};
