import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: null,
    sessions: {}, // { modelKey: [message, ...] }
    currentModel: "deepseek",
    currentTab: "chat",
    modelOptions: [],
  },
  mutations: {
    setUser(state, user) {
      state.user = user;
    },
    setCurrentModel(state, model) {
      state.currentModel = model;
    },
    setCurrentTab(state, tab) {
      state.currentTab = tab;
    },
    setModelOptions(state, options) {
      state.modelOptions = options;
    },
    addMessage(state, { model, message }) {
      if (!state.sessions[model]) state.sessions[model] = [];
      state.sessions[model].push(message);
    },
    setSession(state, { model, messages }) {
      state.sessions[model] = messages;
    },
    clearSession(state, model) {
      state.sessions[model] = [];
    },
  },
});
