<template>
  <div class="container mx-auto pl-4 pr-4">
    <div class="flex flex-col lg:flex-row lg:gap-8 lg:mt-10">
      <!--  -->
      <div
        class="w-full aspect-square mb-4 rounded-md overflow-hidden lg:w-1/2 lg:mb-0"
      >
        <template v-if="imageUrl">
          <img
            :src="imageUrl"
            alt="Uploaded Image"
            class="w-full h-full object-cover"
          />
        </template>
        <template v-else>
          <div
            class="w-full h-full bg-gray-100 flex items-center justify-center"
          >
            <img
              src="@/assets/quote.gif"
              alt=""
              class="w-full h-full object-cover"
            />
          </div>
        </template>
      </div>

      <div class="w-full lg:w-1/2 flex flex-col justify-between">
        <div>
          <!-- <h2 class="head-title font-bold text-left cursor-pointer transition-all">AI</h2> -->

          <textarea
            v-model="inputText"
            class="text-sm w-full h-[80px] p-2 text-lg border rounded-md focus:ring-2"
            placeholder=""
          ></textarea>

          <div class="my-4">
            <label class="block text-lg font-medium mb-2 text-sm"
              >
              <span
                @click="shuffleStyles"
                class="text-blue-500 cursor-pointer float-right"
                ></span
              >
            </label>
            <div class="grid grid-cols-4 gap-1">
              <button
                v-for="style in currentStyles"
                :key="style.name"
                style="font-size: 14px"
                :class="[
                  'p-2 rounded-md border focus:outline-none',
                  selectedStyle === style.name
                    ? 'bg-blue-500 text-white'
                    : 'bg-gray-200',
                ]"
                @click="selectStyle(style.name)"
              >
                {{ style.name }}
              </button>
            </div>
          </div>
        </div>
        <div>
          <button
            @click="submitData"
            class="text-sm w-full bg-blue-500 text-white py-2 px-4 rounded-md hover:bg-blue-600 disabled:bg-gray-300 disabled:cursor-not-allowed transition-all"
            :disabled="!inputText || isLoading"
          >
            {{ isLoading ? "..." : "" }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { API_CONFIG } from "../../utils/api";

import { ImageCogView } from "../../utils/prompt.js";
export default {
  data() {
    return {
      inputText: "",
      selectedStyle: "",
      imageUrl: "",
      isLoading: false,
      styles: ImageCogView,
      currentStyles: [],
    };
  },
  methods: {
    selectStyle(styleName) {
      this.selectedStyle = styleName;
    },
    shuffleStyles() {
      // styles3
      const shuffled = this.styles.sort(() => Math.random() - 0.5).slice(0, 4);
      this.currentStyles = shuffled;
    },
    async submitData() {
      if (!this.inputText) return;

      const { apiUrl, apiKey, modelName } = API_CONFIG["bigmodelCogview"];
      const stylePrompt =
        this.styles.find((style) => style.name === this.selectedStyle)
          ?.prompt || "";
      const fullPrompt = `${this.inputText}${stylePrompt}`;

      await this.getAIResponse(apiUrl, apiKey, modelName, fullPrompt);
    },
    async getAIResponse(apiUrl, apiKey, modelName, prompt) {
      this.isLoading = true;
      this.imageUrl = null;
      try {
        const requestBody = {
          model: modelName,
          prompt,
        };
        const response = await fetch(apiUrl, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${apiKey}`,
          },
          body: JSON.stringify(requestBody),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }

        const result = await response.json();
        this.imageUrl = result.data[0]?.url || "";
      } catch (error) {
        console.error("Error fetching AI response:", error);
      } finally {
        this.isLoading = false;
      }
    },
  },
  mounted() {
    // 3
    this.shuffleStyles();
  },
};
</script>

<style scoped>
.container {
  padding-bottom: 50px;
}
textarea {
  outline: none;
}
.head-title {
  font-size: 16px;
  margin-bottom: 10px;
}
</style>
