<template>
  <div class="container mx-auto pl-4 pr-4">
    <div class="flex flex-col lg:flex-row lg:gap-8 lg:mt-10">
      <!--  -->
      <div
        class="w-full aspect-[4/3] mb-4 rounded-md overflow-hidden lg:w-1/2 lg:mb-0"
      >
        <video
          :src="videoUrl"
          :poster="coverImageUrl"
          controls
          class="w-full h-full object-cover"
          v-if="videoUrl"
        ></video>
        <div
          class="w-full h-full bg-gray-100 flex items-center justify-center"
          v-else
        >
          <img
            src="@/assets/quote.gif"
            alt=""
            style="width: 400px; height: 400px"
          />
        </div>
      </div>

      <div class="w-full lg:w-1/2 flex flex-col justify-between">
        <div>
          <div class="relative">
            <label for="inputText" class="text-sm font-semibold"
              >AI

              <a
                href="javascript:void(0)"
                class="text-blue-500 cursor-pointer float-right"
                @click="optimizeInput"
                ></a
              >
            </label>
            <textarea
              v-model="inputText"
              :disabled="disabled"
              class="text-sm w-full h-[120px] p-2 text-lg border rounded-md focus:ring-2 mb-4 mt-2"
              placeholder=""
            ></textarea>
          </div>
        </div>
        <button
          @click="submitData"
          class="text-sm w-full bg-blue-500 text-white py-2 px-4 rounded-md hover:bg-blue-600 disabled:bg-gray-300 disabled:cursor-not-allowed transition-all"
          :disabled="!inputText || isLoading"
        >
          {{ isLoading ? inputTip : "" }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { API_CONFIG } from "../../utils/api";

export default {
  data() {
    return {
      inputText: "",
      videoUrl: "",
      coverImageUrl: "",
      isLoading: false,
      disabled: false,
      inputTip: "",
    };
  },
  methods: {
    async optimizeInput() {
      if (!this.inputText) return;

      this.isLoading = true;
      try {
        this.inputTip = "";
        const optimizedPrompt = await this.optimizePrompt(this.inputText);
        if (optimizedPrompt) {
          this.inputText = optimizedPrompt;
        }
      } catch (error) {
        console.error("Error optimizing prompt:", error);
      } finally {
        this.isLoading = false;
      }
    },
    async submitData() {
      if (!this.inputText) return;

      // 
      const { apiUrl, apiKey, modelName } = API_CONFIG["bigmodelCogVideo"];
      const fullPrompt = `${this.inputText}`;

      // 
      const taskId = await this.generateVideo(
        apiUrl,
        apiKey,
        modelName,
        fullPrompt
      );
      if (taskId) {
        await this.pollTaskStatus(taskId);
      }
    },
    async optimizePrompt(userInput) {
      try {
        const { apiUrl, apiKey, modelName } = API_CONFIG["bigmodel"];
        const requestBody = {
          model: modelName,
          stream: false,
          temperature: 0.8,
          messages: [
            {
              role: "system",
              content: `

1. 
2. 
3.  150 

### 
****
****""
`,
            },
            { role: "user", content: `${userInput}` },
          ],
        };
        const response = await fetch(apiUrl, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${apiKey}`,
          },
          body: JSON.stringify(requestBody),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }

        const result = await response.json();
        return result.choices[0].message.content || "";
      } catch (error) {
        console.error("Error optimizing prompt:", error);
        return null;
      }
    },
    async generateVideo(apiUrl, apiKey, modelName, prompt) {
      this.inputTip = "";
      this.isLoading = true;
      this.videoUrl = null;
      try {
        const requestBody = {
          model: modelName,
          prompt,
          with_audio: true,
        };
        const response = await fetch(apiUrl, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${apiKey}`,
          },
          body: JSON.stringify(requestBody),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }

        const result = await response.json();

        return result.id; //  ID  task_id
      } catch (error) {
        console.error("Error generating video:", error);
        return null;
      }
    },
    async pollTaskStatus(taskId) {
      const { apiKey } = API_CONFIG["bigmodelCogVideo"];
      const maxAttempts = 30; // 
      const interval = 5000; //  5 

      for (let i = 0; i < maxAttempts; i++) {
        try {
          const response = await fetch(
            `https://open.bigmodel.cn/api/paas/v4/async-result/${taskId}`,
            {
              method: "GET",
              headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${apiKey}`,
              },
            }
          );

          if (!response.ok) {
            throw new Error(`HTTP error! Status: ${response.status}`);
          }

          const result = await response.json();
          if (result.task_status === "SUCCESS") {
            this.isLoading = false;
            this.videoUrl = result.video_result[0]?.url;
            this.coverImageUrl = result.video_result[0]?.cover_image_url;
            return;
          } else if (result.status === "failed") {
            throw new Error("Video generation failed");
          }
        } catch (error) {
          console.error("Error polling task status:", error);
          return;
        }

        await new Promise((resolve) => setTimeout(resolve, interval));
      }

      console.error("Task polling timed out");
    },
  },
  mounted() {},
};
</script>

<style scoped>
.container {
  padding-bottom: 50px;
}
textarea {
  outline: none;
}
.head-title {
  font-size: 18px;
  margin-bottom: 10px;
}
</style>
