</template>

<script>
import { fetchAIResponse, API_CONFIG, fetchTTS } from "../../utils/api";
import html2canvas from "html2canvas";
import { ImagePrompts } from "../../utils/prompt.js";

export default {
  data() {
    return {
      imageUrl: null, //  URL
      imageBase64: null, //  Base64 
      generatedContent: null,
      isLoading: false, // 
      isShareDialogOpen: false, // 
      selectedFunction: 0, // 
      functions: ImagePrompts,
      abortController: null, //  AbortController 
      audioUrl: null, //  URL
    };
  },
  methods: {
    // 
    switchFunction() {
      this.selectedFunction =
        (this.selectedFunction + 1) % this.functions.length;
    },
    async handleImageUpload(event) {
      this.generatedContent = null;
      const file = event.target.files[0];
      if (!file) return;

      // 
      const allowedTypes = ["image/jpg", "image/jpeg", "image/png"];
      if (!allowedTypes.includes(file.type)) {
        alert(" JPGJPEGPNG ");
        return;
      }

      //  6000x6000
      const img = new Image();
      img.src = URL.createObjectURL(file);
      img.onload = async () => {
        const width = img.width;
        const height = img.height;
        if (width > 6000 || height > 6000) {
          alert(" 6000x6000");
          return;
        }

        //  5MB
        if (file.size > 5 * 1024 * 1024) {
          try {
            const compressedFile = await this.compressImage(file);
            this.imageUrl = URL.createObjectURL(compressedFile);
            this.imageBase64 = await this.fileToBase64(compressedFile);
          } catch (error) {
            console.error(":", error);
            alert("");
            return;
          }
        } else {
          //  5MB 
          const reader = new FileReader();
          reader.onload = (e) => {
            this.imageUrl = e.target.result; // 
            this.imageBase64 = e.target.result.split(",")[1]; //  Base64 
          };
          reader.readAsDataURL(file);
        }
      };
    },
    // 
    compressImage(file) {
      return new Promise((resolve, reject) => {
        const img = new Image();
        img.src = URL.createObjectURL(file);
        img.onload = () => {
          const canvas = document.createElement("canvas");
          const ctx = canvas.getContext("2d");

          // 
          const maxWidth = 6000;
          const maxHeight = 6000;
          let width = img.width;
          let height = img.height;

          if (width > maxWidth || height > maxHeight) {
            const ratio = Math.min(maxWidth / width, maxHeight / height);
            width *= ratio;
            height *= ratio;
          }

          canvas.width = width;
          canvas.height = height;

          //  canvas
          ctx.drawImage(img, 0, 0, width, height);

          //  canvas  Blob
          canvas.toBlob(
            (blob) => {
              if (!blob) {
                reject(new Error(""));
                return;
              }

              // 
              if (blob.size > 5 * 1024 * 1024) {
                //  5MB
                canvas.toBlob(
                  (newBlob) => {
                    if (!newBlob) {
                      reject(new Error(""));
                      return;
                    }
                    resolve(newBlob);
                  },
                  file.type,
                  0.5 // 
                );
              } else {
                resolve(blob);
              }
            },
            file.type,
            0.8 // 
          );
        };
        img.onerror = (error) => reject(error);
      });
    },
    //  Base64
    fileToBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = (e) => resolve(e.target.result.split(",")[1]);
        reader.onerror = (error) => reject(error);
        reader.readAsDataURL(file);
      });
    },
    async startRecognition() {
      //  AbortController 
      this.abortController = new AbortController();
      if (!this.imageBase64) {
        alert("");
        return;
      }

      this.isLoading = true; // 

      try {
        const messages = [
          {
            role: "user",
            content: [
              {
                type: "image_url",
                image_url: {
                  url: `data:image/jpeg;base64,${this.imageBase64}`, //  Base64 
                },
              },
              {
                type: "text",
                text: this.functions[this.selectedFunction].prompt,
              },
            ],
          },
        ];
        const { apiUrl, apiKey, modelName, temperature } =
          API_CONFIG["stepfun"];

        const stream = true;
        // 
        let streamContent = "";
        await fetchAIResponse(
          apiUrl,
          apiKey,
          modelName,
          messages,
          temperature,
          stream,
          (chunk) => {
            if (this.abortController.signal.aborted) {
              return; // 
            }
            // 
            if (chunk.type === "content") {
              streamContent += chunk.content;
              this.generatedContent = streamContent;
            }
          },
          this.abortController
        );
        // this.generatedContent = data.choices[0].message.content

        // 
        const cleanedContent = streamContent.replace(
          /[\u{1F600}-\u{1F64F}]/gu,
          ""
        );
        // 
        const trimmedContent = cleanedContent.replace(/["\s]/g, "");
        //  generateTTS
        this.generateTTS(trimmedContent);
      } catch (error) {
        console.error("Error fetching AI response:", error);
        this.generatedContent = "";
      } finally {
        this.isLoading = false; // 
      }
    },
    // tts
    async generateTTS(content) {
      const { apiUrl, apiKey, modelName } = API_CONFIG["stepfunTTS"];
      const input = content;
      const voice = "jilingshaonv";

      try {
        const blob = await fetchTTS(apiUrl, apiKey, modelName, input, voice);
        this.audioUrl = URL.createObjectURL(blob);
        // 
        this.$nextTick(() => {
          const audioElement = this.$el.querySelector("audio");
          if (audioElement) {
            audioElement.play();
          }
        });
      } catch (error) {
        console.error("Error generating speech:", error);
      }
    },
    playAudio() {
      const audio = this.$refs.audioRef;
      if (audio) {
        audio.play().catch((error) => {
          console.error(":", error);
        });
      }
    },
    // 
    openShareDialog() {
      this.isShareDialogOpen = true;
    },

    // 
    closeShareDialog() {
      this.isShareDialogOpen = false;
    },

    // 
    saveShareToLocal() {
      const postCardElement = document.querySelector(".post-card"); //  .post-card 
      if (!postCardElement) {
        alert("");
        return;
      }
      //  html2canvas 
      html2canvas(postCardElement, {
        allowTaint: true, // 
        useCORS: true, //  CORS
        scale: 2, // 
        onclone: (clonedDoc) => {
          // 
          const clonedImg = clonedDoc.querySelector(".image-preview img");
          if (clonedImg) {
            // 
            clonedImg.style.objectFit = "cover";
            clonedImg.style.width = "100%";
            clonedImg.style.height = "100%";
          }
        },
      })
        .then((canvas) => {
          //  canvas  Data URL
          const imageUrl = canvas.toDataURL("image/png");

          // 
          const link = document.createElement("a");
          link.href = imageUrl;
          link.download = "pet-conversation.png"; // 
          link.click(); // 
        })
        .catch((error) => {
          console.error(":", error);
          alert("");
        });
    },
  },
};
</script>

