<template>
  <div
    class="min-h-screen flex flex-col absolute inset-0 -z-10 h-full w-full bg-main"
  >
    <div
      class="main min-h-screen flex flex-col absolute inset-0 -z-10 h-full w-full"
    >
      <!--  -->
      <TabSelector :tab="tab" @tab-selected="changeTab" />
      <template v-if="tab === 'chat'">
        <!-- <ModelSelector v-if="!isDeepThinking" :model="model" @change-model="changeModel" /> -->

        <!--  -->
        <div
          ref="chatContainer"
          class="flex-1 p-2 overflow-y-auto pb-2 chatContainer"
        >
          <!--  -->
          <Message
            v-for="message in messages"
            :key="message.id"
            :message="message"
          />
        </div>

        <!--  -->
        <InputBox
          ref="inputBox"
          :is-thinking="isThinking"
          :showMode="true"
          :mode="mode"
          :model="model"
          :is-deep-thinking="isDeepThinking"
          @send-message="sendMessage"
          @scroll-to-bottom="scrollToBottom"
          @change-mode="changeMode"
          @toggle-deep-thinking="toggleDeepThinking"
          @change-model="changeModel"
          @stop-ai-response="stopAIResponse"
        />
      </template>
      <!-- - -->
      <template v-else-if="tab === 'image'">
        <Glm4V />
      </template>
      <!--  -->
      <template v-else-if="tab === 'cogview'">
        <CogView />
      </template>
      <template v-else-if="tab === 'ragchat'">
        <RagChat />
      </template>
      <!--  -->
      <template v-else-if="tab === 'textvideo'">
        <VideoGlm />
      </template>
      <template v-else-if="tab === 'imgvideo'">
        <VideoImg />
      </template>
      <FooterBox />
    </div>
  </div>
</template>

<script>
// import ModelSelector from './ModelSelector.vue'
import { mapState } from "vuex";
import TabSelector from "./TabSelector.vue";
import Message from "./ChatBox/MessageBox.vue";
import InputBox from "./ChatBox/InputBox.vue";
import Glm4V from "./ImgBox/Glm4V.vue";
import CogView from "./ImgBox/CogView.vue";
import VideoGlm from "./ImgBox/VideoGlm.vue";
import VideoImg from "./ImgBox/VideoImg.vue";
import FooterBox from "./FooterBox.vue";
import { fetchAIResponse, API_CONFIG } from "../utils/api";
import { ChatPrompts } from "../utils/prompt.js";
import RagChat from "./ChatBox/RagChat.vue";
export default {
  components: {
    // ModelSelector,
    TabSelector,
    Message,
    InputBox,
    FooterBox,
    Glm4V,
    CogView,
    VideoGlm,
    VideoImg,
    RagChat,
  },
  ...mapState(["sessions", "currentModel", "currentTab"]),
  data() {
    return {
      messages: [], // 
      isThinking: false,
      mode: "normal",
      model: "deepseek",
      tab: "chat",
      abortController: null, //  AbortController 
      isDeepThinking: true, //  data 
    };
  },
  methods: {
    toggleDeepThinking(isDeepThinking) {
      this.isDeepThinking = isDeepThinking;
      if (isDeepThinking) {
        this.model = "deepThinking";
      } else {
        this.model = "deepseek";
      }
    },
    async sendMessage(userInput) {
      if (userInput.trim() === "") return;

      // 
      this.messages.push({
        role: "user",
        content: userInput,
        id: Date.now(),
      });

      const loadingMessage = {
        role: "assistant",
        content: "",
        id: "loading-" + Date.now(),
      };
      this.messages.push(loadingMessage);

      this.isThinking = true;

      //  AbortController 
      this.abortController = new AbortController();

      //  AI  abortController
      await this.getAIResponse(loadingMessage.id, this.abortController);

      this.isThinking = false;
      this.scrollToBottom();
    },
    async getAIResponse(loadingMessageId, controller) {
      try {
        const systemMessage = this.getSystemMessage();

        // 
        const messagesWithoutDefault = this.messages.filter(
          (msg) =>
            msg.role !== "assistant" ||
            msg.content !== ""
        );

        const messages = [
          { role: "system", content: systemMessage },
          ...messagesWithoutDefault
            .filter((msg) => msg.id !== loadingMessageId)
            .map((msg) => ({ role: msg.role, content: msg.content })),
        ];

        const { apiUrl, apiKey, modelName, temperature } = this.getApiConfig();

        let reasoningContent = "";
        let finalContent = "";
        let totalTokens = 0;

        let isInsideThinkTag = false; //  <think> 
        let thinkBuffer = ""; //  <think> 

        const index = this.messages.findIndex(
          (msg) => msg.id === loadingMessageId
        );
        if (index !== -1) {
          this.messages = [
            ...this.messages.slice(0, index),
            {
              role: "assistant",
              content: "",
              reasoningContent: "",
              token: 0,
              duration: 0,
              id: this.generateUniqueId(),
              mode: this.mode,
              model: this.model,
              reasoningSearchResults: [], //  reasoningSearchResults
              contentSearchResults: [], //  contentSearchResults
            },
            ...this.messages.slice(index + 1),
          ];
        }

        const stream = true;
        await fetchAIResponse(
          apiUrl,
          apiKey,
          modelName,
          messages,
          temperature,
          stream,
          (chunk) => {
            if (controller.signal.aborted) {
              return; // 
            }

            const extractSearchResults = (text) => {
              //  RegExp 
              const searchResultRegex = new RegExp(
                "([\\s\\S]*?)\\$\\~\\~\\~\\$\\s*(\\[.*?\\])\\s*\\$\\~\\~\\~\\$",
                "g"
              );
              let match;
              let extractedResults = [];

              while ((match = searchResultRegex.exec(text)) !== null) {
                try {
                  const parsedResults = JSON.parse(match[2]);
                  extractedResults = [...extractedResults, ...parsedResults]; // 
                } catch (error) {
                  console.error("JSON :", error);
                  console.error(":", match[2]);
                }
              }
              return extractedResults;
            };

            // 
            const removeSearchResultMarkers = (text) => {
              const searchResultRegex = new RegExp(
                "([\\s\\S]*?)\\$\\~\\~\\~\\$\\s*(\\[.*?\\])\\s*\\$\\~\\~\\~\\$",
                "g"
              );
              return text.replace(searchResultRegex, "");
            };

            if (chunk.type === "reasoning") {
              reasoningContent += chunk.content;
              totalTokens = parseFloat((totalTokens + chunk.token).toFixed(4));

              // 
              const currentMessage = this.messages[index];

              //  reasoning 
              if (!currentMessage.reasoningStartTime) {
                currentMessage.reasoningStartTime = Date.now();
              }

              // 
              const currentTime = Date.now();
              currentMessage.reasoningDuration = parseFloat(
                (
                  (currentTime - currentMessage.reasoningStartTime) /
                  1000
                ).toFixed(1)
              );

              //  reasoningSearchResults 
              currentMessage.reasoningSearchResults = [];
              const results = extractSearchResults(reasoningContent);
              currentMessage.reasoningSearchResults = [...results];

              //  reasoningContent 
              const cleanedReasoningContent =
                removeSearchResultMarkers(reasoningContent);

              this.messages = [
                ...this.messages.slice(0, index),
                {
                  ...currentMessage,
                  reasoningContent: cleanedReasoningContent,
                  token: totalTokens,
                  duration: chunk.duration,
                }, //  reasoningSearchResults
                ...this.messages.slice(index + 1),
              ];
            } else if (chunk.type === "content") {
              // 
              const currentMessage = this.messages[index]; // 

              // 
              const content = chunk.content;

              //  <think> 
              if (content.includes("<think>")) {
                isInsideThinkTag = true; //  <think> 
                thinkBuffer = ""; // 

                // 
                if (!currentMessage.reasoningStartTime) {
                  currentMessage.reasoningStartTime = Date.now();
                }

                //  content  ""
                currentMessage.content = "";
              }

              //  <think>  reasoningContent
              if (isInsideThinkTag) {
                thinkBuffer += content;

                //  <think>  <think> 
                const thinkContent = thinkBuffer.replace("<think>", ""); // 
                // .replace(/\n/g, '') // 

                reasoningContent = thinkContent; //  reasoningContent

                // 
                if (currentMessage.reasoningStartTime) {
                  const currentTime = Date.now();
                  currentMessage.reasoningDuration = parseFloat(
                    (
                      (currentTime - currentMessage.reasoningStartTime) /
                      1000
                    ).toFixed(1)
                  );
                }
              }

              //  </think> 
              if (content.includes("</think>")) {
                isInsideThinkTag = false; //  <think> 

                //  <think> 
                const thinkContent = thinkBuffer
                  .replace("<think>", "") // 
                  .replace("</think>", ""); // 
                // .replace(/\n/g, '') // 

                reasoningContent = thinkContent; //  reasoningContent
                thinkBuffer = ""; // 
              }

              //  <think>  finalContent
              if (
                !isInsideThinkTag &&
                !content.includes("<think>") &&
                !content.includes("</think>")
              ) {
                finalContent += content;
              }

              totalTokens = parseFloat((totalTokens + chunk.token).toFixed(4));

              //  contentSearchResults 
              currentMessage.contentSearchResults = [];
              const results = extractSearchResults(finalContent);
              currentMessage.contentSearchResults = [...results];

              //  finalContent 
              const cleanedFinalContent =
                removeSearchResultMarkers(finalContent);

              // 
              this.messages = [
                ...this.messages.slice(0, index),
                {
                  ...currentMessage,
                  content: isInsideThinkTag ? "" : cleanedFinalContent, //  ""
                  reasoningContent: reasoningContent, //  reasoningContent
                  token: totalTokens,
                  duration: chunk.duration,
                },
                ...this.messages.slice(index + 1),
              ];
            } else if (chunk.type === "complete") {
              const currentMessage = this.messages[index];
              this.messages = [
                ...this.messages.slice(0, index),
                { ...currentMessage, duration: chunk.duration },
                ...this.messages.slice(index + 1),
              ]; //  reasoningSearchResults  contentSearchResults
            }
            this.conditionalScrollToBottom();
          },
          controller
        );
      } catch (error) {
        if (error.name !== "AbortError") {
          this.messages = [
            {
              role: "assistant",
              content: `\`\`\`${error.message}\n${error.name}\n${error.stack}\`\`\``,
              id: this.generateUniqueId(),
            },
          ];
        }
      }
    },
    conditionalScrollToBottom() {
      const chatContainer = this.$refs.chatContainer;
      if (!chatContainer) return; // Ensure container exists

      const isAtBottom =
        chatContainer.scrollHeight - chatContainer.scrollTop <=
        chatContainer.clientHeight + 50; // Add a tolerance of 50px

      if (isAtBottom) {
        this.scrollToBottom();
      }
    },
    scrollToBottom() {
      this.$nextTick(() => {
        const chatContainer = this.$refs.chatContainer;
        chatContainer.scrollTop = chatContainer.scrollHeight;
      });
    },
    generateUniqueId() {
      return Date.now().toString(36) + Math.random().toString(36).substring(2);
    },
    getSystemMessage() {
      if (this.model === "qwq") {
        return ""; // systemMessage 
      }
      const prompt = ChatPrompts.find((p) => p.mode === this.mode);
      return prompt
        ? prompt.systemMessage
        : "";
    },
    getApiConfig() {
      // 
      const config = API_CONFIG[this.model];
      if (!config) {
        throw new Error(` ${this.model} `);
      }
      return config;
    },
    changeMode(newMode) {
      if (this.abortController) {
        this.abortController.abort();
      }
      this.mode = newMode;
      this.messages = []; // 
      // 
      this.isThinking = false;
    },
    changeModel(newModel) {
      if (this.abortController) {
        this.abortController.abort();
      }
      this.model = newModel;
      if (newModel === "shuinifengxin") {
        this.mode = "rude";
      } else {
        this.mode = "normal";
      }
      this.messages = []; // 
      // 
      this.isThinking = false;
      this.$refs.inputBox.init();
    },
    changeTab(newTab) {
      this.tab = newTab;
      this.isDeepThinking = false;
      this.model = "deepseek";
      this.messages = [];
      // 
    },
    stopAIResponse() {
      if (this.abortController) {
        this.abortController.abort();
        this.isThinking = false;
        // 
        this.messages.push({
          role: "assistant",
          model: this.model,
          content: "",
        });
      } else {
        console.log("No AI response in progress to stop.");
      }
    },
  },
  mounted() {
    // Urlmodel this.model
    const urlParams = new URLSearchParams(window.location.search);
    const modelParam = urlParams.get("model");
    const tabParam = urlParams.get("tab");
    if (modelParam) {
      this.model = modelParam;
    }
    if (tabParam) {
      this.tab = tabParam;
    }
    // 
    // if (!this.isDeepThinking) {
    //     this.insertDefaultMessage()
    // }
    // 
    this.isDeepThinking = true;
    this.model = "deepThinking";
  },
};
</script>
