<script>
export default {
  data() {
    return {
      messages: [],
      isThinking: false,
      mode: "normal",
      model: "ragchat", // ragchat
      abortController: null,
      isDeepThinking: false,
      userInput: "",
      boxHeight: 100,
      initialHeight: 100,
    };
  },
  methods: {
    toggleDeepThinking(isDeepThinking) {
      this.isDeepThinking = isDeepThinking;
    },
    async sendMessage(userInput) {
      if (userInput.trim() === "") return;
      //  ragchat 
      this.messages.push({
        role: "user",
        content: userInput,
        id: Date.now(),
      });
      const loadingMessage = {
        role: "assistant",
        content: "",
        id: "loading-" + Date.now(),
      };
      this.messages.push(loadingMessage);
      this.isThinking = true;
      this.abortController = new AbortController();
      await this.getAIResponse(loadingMessage.id, this.abortController);
      this.isThinking = false;
      this.scrollToBottom();
    },
    async getAIResponse(loadingMessageId, controller) {
      try {
        // / prompt
        const messages = this.messages
          .filter((msg) => msg.id !== loadingMessageId)
          .map((msg) => ({ role: msg.role, content: msg.content }));
        //  loadingMessageId 
        const index = this.messages.findIndex(
          (msg) => msg.id === loadingMessageId
        );
        // API
        const apiUrl =
          "https://dashscope.aliyuncs.com/api/v1/apps/b06082ea08e14da99c6803f6a4742edc/completion";
        const apiKey = "sk-57d82161014a48e1861aace48d89f189"; // key
        const body = {
          input: {
            prompt: messages,
          },
          parameters: {
            incremental_output: true,
            rag_options: {
              pipeline_ids: [
                "cmupp61ie0",
                "efud9ymho4",
                "e0383orm79",
                "v4fz5025s9",
              ],
            },
            has_thoughts: false,
          },
          debug: {},
        };
        //  fetch
        const response = await fetch(apiUrl, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${apiKey}`,
            "X-DashScope-SSE": "enable",
          },
          body: JSON.stringify(body),

          signal: controller ? controller.signal : undefined,
        });
        if (!response.body || !window.ReadableStream) {
          // 
          const data = await response.json();
          let aiContent =
            data.output ||
            data.choices?.[0]?.message?.content ||
            JSON.stringify(data);
          this.messages = [
            ...this.messages.slice(0, index),
            {
              role: "assistant",
              content: aiContent,
              id: this.generateUniqueId(),
              mode: this.mode,
              model: this.model,
            },
            ...this.messages.slice(index + 1),
          ];
          return;
        }
        // 
        const reader = response.body.getReader();
        let aiContent = "";
        let done = false;
        while (!done) {
          const { value, done: doneReading } = await reader.read();
          done = doneReading;
          if (value) {
            const chunk = new TextDecoder().decode(value);
            //  SSE  data: 
            chunk.split("\n").forEach((line) => {
              if (line.startsWith("data:")) {
                const dataStr = line.replace(/^data:/, "").trim();
                if (dataStr && dataStr !== "[DONE]") {
                  try {
                    const json = JSON.parse(dataStr);
                    // outputtext
                    if (typeof json.output === "string") {
                      aiContent += json.output;
                    } else if (
                      json.output &&
                      typeof json.output.text === "string"
                    ) {
                      aiContent += json.output.text;
                    } else if (Array.isArray(json.choices)) {
                      json.choices.forEach((choice) => {
                        if (typeof choice.text === "string") {
                          aiContent += choice.text;
                        } else if (
                          choice.message &&
                          typeof choice.message.content === "string"
                        ) {
                          aiContent += choice.message.content;
                        }
                      });
                    }
                  } catch (e) {
                    // 
                    aiContent += dataStr;
                  }
                }
              }
            });
            // 
            this.messages = [
              ...this.messages.slice(0, index),
              {
                role: "assistant",
                content: aiContent,
                id: this.generateUniqueId(),
                mode: this.mode,
                model: this.model,
              },
              ...this.messages.slice(index + 1),
            ];
          }
        }
      } catch (error) {
        this.messages = [
          {
            role: "assistant",
            content: `${error.message}`,
            id: this.generateUniqueId(),
          },
        ];
      }
    },
    scrollToBottom() {
      this.$nextTick(() => {
        const chatContainer = this.$refs.chatContainer;
        if (chatContainer) chatContainer.scrollTop = chatContainer.scrollHeight;
      });
    },
    generateUniqueId() {
      return Date.now().toString(36) + Math.random().toString(36).substring(2);
    },
    getSystemMessage() {
      return "RAG";
    },
    changeMode(newMode) {
      this.mode = newMode;
      this.messages = [];
      this.isThinking = false;
    },
    changeModel(newModel) {
      this.model = newModel;
      this.messages = [];
      this.isThinking = false;
      this.$refs.inputBox &&
        this.$refs.inputBox.init &&
        this.$refs.inputBox.init();
    },
    stopAIResponse() {
      if (this.abortController) {
        this.abortController.abort();
        this.isThinking = false;
        this.messages.push({
          role: "assistant",
          model: this.model,
          content: "",
        });
      }
    },
    handleInput(event) {
      this.autoResizeTextarea(event.target);
      this.adjustHeight();
    },
    handleKeydown(event) {
      if (event.key === "Enter" && !event.ctrlKey && !event.metaKey) {
        event.preventDefault();
        this.handleSend();
      } else if (event.key === "Enter" && (event.ctrlKey || event.metaKey)) {
        event.preventDefault();
        this.userInput += "\n";
        this.autoResizeTextarea(this.$refs.textarea);
      }
    },
    handleSend() {
      if (this.userInput.trim() === "" || this.isThinking) return;
      this.sendMessage(this.userInput);
      this.userInput = "";
      this.resetTextareaHeight(this.$refs.textarea);
      this.$nextTick(() => this.scrollToBottom());
    },
    autoResizeTextarea(textarea) {
      textarea.style.height = "auto";
      textarea.style.height = textarea.scrollHeight + "px";
      this.boxHeight = textarea.scrollHeight + 55;
    },
    adjustHeight() {
      const textarea = this.$refs.textarea;
      textarea.style.height = "auto";
      textarea.style.height = textarea.scrollHeight + "px";
      this.boxHeight = textarea.scrollHeight + 55;
    },
    resetTextareaHeight(textarea) {
      textarea.style.height = "auto";
      textarea.style.height = this.initialHeight + "px";
      this.boxHeight = this.initialHeight;
    },
    renderMarkdown(content) {
      //  markdown 
      if (!content) return "";
      //  marked/highlight.js
      // ```lang\ncode\n```
      content = content.replace(
        /```([\w-]*)\n([\s\S]*?)```/g,
        function (match, lang, code) {
          return `<pre class='code-block'><code>${escapeHtml(
            code
          )}</code></pre>`;
        }
      );
      // 
      content = content.replace(/`([^`]+)`/g, "<code>$1</code>");
      // 
      content = content
        .replace(/\*\*(.*?)\*\*/g, "<b>$1</b>")
        .replace(/\*(.*?)\*/g, "<i>$1</i>")
        .replace(/\n/g, "<br>");
      return content;
      function escapeHtml(str) {
        return str.replace(/[&<>"']/g, function (tag) {
          const chars = {
            "&": "&amp;",
            "<": "&lt;",
            ">": "&gt;",
            '"': "&quot;",
            "'": "&#39;",
          };
          return chars[tag] || tag;
        });
      }
    },
  },
};
</script>

