import Vue from "vue";

import VueRouter from "vue-router";
import ChatBox from "../components/ChatBox.vue";
import CogView from "@/components/ImgBox/CogView.vue";
import Glm4V from "@/components/ImgBox/Glm4V.vue";
import RagChat from "@/components/ChatBox/RagChat.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "ChatBox",
    component: ChatBox,
  },
  {
    path: "/pagereader",
    name: "Glm4v",
    component: Glm4V,
  },
  {
    path: "/pagedesigner",
    name: "CogView",
    component: CogView,
  },
  {
    path: "/ragchat",
    name: "RagChat",
    component: RagChat,
  },
];

const router = new VueRouter({
  mode: "history",
  routes,
});

export default router;
