vue
<template>
  <div class="code-block">
    <!--  -->
    <pre><code ref="code" :class="languageClass">{{ formattedCode }}</code></pre>
    <!--  -->
    <svg
      v-if="!isCopied"
      @click="copyCode"
      xmlns="http://www.w3.org/2000/svg"
      class="h-5 w-5 text-gray-500 absolute top-2 right-2 cursor-pointer copy-button"
      fill="none"
      viewBox="0 0 24 24"
      stroke="currentColor"
    >
      <path
        stroke-linecap="round"
        stroke-linejoin="round"
        stroke-width="2"
        d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z"
      />
    </svg>
    <!--  -->
    <svg
      v-else
      xmlns="http://www.w3.org/2000/svg"
      class="h-5 w-5 text-green-500 absolute top-2 right-2 cursor-pointer copy-button"
      fill="none"
      viewBox="0 0 24 24"
      stroke="currentColor"
    >
      <path
        stroke-linecap="round"
        stroke-linejoin="round"
        stroke-width="2"
        d="M5 13l4 4L19 7"
      />
    </svg>
  </div>
</template>
<script>
import hljs from "highlight.js"; //  highlight.js
import "highlight.js/styles/atom-one-dark.css"; // 
import Clipboard from "clipboard"; //  clipboard 
export default {
  props: {
    code: {
      type: String,
      required: true,
    },
    language: {
      type: String,
      default: "plaintext", // 
    },
    showCopyButton: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      isCopied: false, // 
    };
  },
  computed: {
    // 
    languageClass() {
      return `language-${this.language}`;
    },
    // 
    formattedCode() {
      return this.code; // 
    },
  },
  mounted() {
    // 
    this.highlightCode();
  },
  methods: {
    // 
    highlightCode() {
      const codeElement = this.$refs.code;
      if (codeElement) {
        hljs.highlightBlock(codeElement);
      }
    },
    // 
    copyCode() {
      const clipboard = new Clipboard(".copy-button", {
        text: () => this.code,
      });
      clipboard.on("success", () => {
        this.isCopied = true; // 
        setTimeout(() => {
          this.isCopied = false; // 3 
        }, 3000);
        clipboard.destroy(); //  clipboard 
      });
      clipboard.on("error", () => {
        console.log("");
        clipboard.destroy(); //  clipboard 
      });
    },
  },
};
</script>
<style scoped>
.code-block {
  position: relative;
  background: #282c34; /*  */
  border-radius: 6px;
  padding: 10px;
  margin: 16px 0;
}
pre {
  margin: 0;
  overflow-x: auto; /*  */
  white-space: pre-wrap; /*  */
  word-wrap: break-word; /*  */
}
code {
  display: block; /*  */
  font-family: "Courier New", Courier, monospace; /*  */
  font-size: 14px;
  line-height: 1.5;
}
pre code.hljs {
  padding: 0 !important;
}
</style>
